<template lang="pug">
div.parent
  canvas(
    ref='the-canvas' 
    :class='classes'
    @click='canvasClick'
  )
    CPOP-canvas-point(
      v-for='(point, index) in myPoints'
      :key='index'
      :index='index'
      :point='point'
      :canvasNum='canvasNum'
      @drawEvent='redrawFrame'
      )  
  hr
  div.children
    div.leftChild
      CPOP-canvas(
        v-if='leftChildExists'
        :canvasNum='getLeftChildCanvasNum(canvasNum)'
        :parentCanvasNum='canvasNum'
      )
    div.rightChild
      CPOP-canvas(
        v-if='rightChildExists'
        :canvasNum='getRightChildCanvasNum(canvasNum)'
        :parentCanvasNum='canvasNum'
      )
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import NiceCanvas from '../nice-things/Nice-Canvas';
import CPOPCanvasController from './CPOP-CanvasController';
import CPOPCanvasPoint from './CPOP-CanvasPoint';

const { mapState, mapGetters, mapActions } = createNamespacedHelpers('closestPairOfPoints');

export default {
  mixins: [NiceCanvas],
  name: 'CPOP-canvas',
  components: {
    CPOPCanvasController,
    CPOPCanvasPoint,
  },
  props: [
    'canvasNum',
    'parentCanvasNum',
  ],
  computed: {
    ...mapState([
      'pointRadius',
      'valueRange',
      'solver',
      'problemTree',
      'randomRange',
    ]),
    classes() {
      return {
        highlightable: this.solving && !this.selected,
        selected: this.selected && this.solving,
        editing: !this.solving,
      };
    },
    ...mapGetters([
      'solving',
      'getLeftChildCanvasNum',
      'getRightChildCanvasNum',
    ]),
    canvasHeight() {
      return this.valueRange.max - this.valueRange.min + this.extraSpace;
    },
    canvasWidth() {
      if (this.canvasNum === 0) {
        // The root problem has the whole range as its width
        return this.valueRange.max - this.valueRange.min + this.extraSpace;
      }
      return this.myPoints[this.problem.size - 1].x - this.myPoints[0].x + this.extraSpace;
    },
    extraSpace() {
      return this.pointRadius * 10;
    },
    problem() {
      return this.problemTree[this.canvasNum].problem;
    },
    allPoints() {
      return this.$store.state.closestPairOfPoints.points;
    },
    myPoints() {
      if (this.canvasNum === 0) {
        return this.allPoints;
      }
      return this.problem.points;
    },
    selected() { return this.canvasNum === this.solver.canvasNum; },
    leftChildExists() {
      const childCanvasNum = this.getLeftChildCanvasNum(this.canvasNum);
      return this.problemTree[childCanvasNum] !== undefined;
    },
    rightChildExists() {
      const childCanvasNum = this.getRightChildCanvasNum(this.canvasNum);
      return this.problemTree[childCanvasNum] !== undefined;
    },
  },
  data() {
    return {
      ctx: null,
    };
  },
  methods: {
    ...mapActions([
      'selectCanvas',
      'addPoint',
    ]),
    canvasClick(event) {
      if (this.solving) this.selectMe();
      else this.addCanvasPoint(event);
    },
    selectMe() {
      if (this.solving) this.selectCanvas({ canvasNum: this.canvasNum });
    },
    clearCanvas() {
      // erase the canvas
      const a = this.valueRange.max - this.valueRange.min + 2 * this.extraSpace;
      const b = this.valueRange.max - this.valueRange.min + 2 * this.extraSpace;
      this.provider.context.clearRect(-this.extraSpace, -this.extraSpace, a, b);
      let range = this.randomRange;
      if(!this.solving) {
        this.provider.context.save();
        this.provider.context.fillStyle = 'lightgray';
        this.provider.context.fillRect(range.x, range.y, range.w, range.h);
        this.provider.context.restore();
      }
    },
    redrawFrame() {
      this.clearCanvas();
      // all your children redraw themselves
      this.$children.forEach(child => {
        if (typeof child.draw === 'function') child.draw();
      });
    },
    // add a point by clicking on canvas
    addCanvasPoint(event) {
      if (!this.solving) {
        let x = event.offsetX - this.extraSpace / 2;
        let y = event.offsetY - this.extraSpace / 2;
        y = this.valueRange.max - y;
        x = Math.max(x, this.valueRange.min);
        x = Math.min(x, this.valueRange.max);
        y = Math.max(y, this.valueRange.min);
        y = Math.min(y, this.valueRange.max);

        this.addPoint({ point: { x, y } } );
      }
    }

  },
  watch: {
    solving() {
      this.redrawFrame();
    },
    randomRange() {
      this.redrawFrame();
    },
    allPoints(newVal) {
      // If all the points get deleted, the last point remains drawn
      // So we need to call the clearCanvas method in this case
      if(newVal.length === 0) {
        this.clearCanvas();
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      let leftEdge = -this.myPoints[0].x;
      if (this.canvasNum === 0) leftEdge = 0;
      this.ctx = this.$refs['the-canvas'].getContext('2d');
      // translate down so that y = 0 is at the bottom not the top
      this.ctx.translate(0, this.canvasHeight -  0.5 * this.extraSpace);
      // translate left by the leftmost x so that the origin is left of the canvas.
      // This way, the leftmost point will be drawn at the left edge of the canvas.
      this.ctx.translate(leftEdge + 0.5 * this.extraSpace, 0);
      // make the y-axis upside down so that positive y-values go up
      this.ctx.scale(1, -1);
      this.redrawFrame();
    });
  },
};
</script>


<style scoped>
canvas {
  outline: 1px solid black;
  background-color: gray;
}
canvas.highlightable:hover {
  cursor: pointer;
  background-color: lightgray;
}
canvas.selected {
  background-color: lightcyan;
} 
div.leftChild, div.rightChild {
  display: inline-block;
  vertical-align: top;
}
div.parent {
  display: inline-block;
  /* text-align: center; */
  /* overflow: visible; */
}
.editing {
  background-color: lightyellow;
  cursor: crosshair;
}
</style>



