<template lang='pug'>
div
  div.row
    div.col-xs-12.text-center
      h3 Tentatively Matched
  div.row    
    div(v-if='n > 3')
      div.col-xs-6
        div.row(v-for='i in n' v-if='i <= (n + 1) / 2')
          h4 {{i}}.)
          div.matching(v-if='tentative[i-1]')
            SM-person-box(
              :gender='"m"'
              :index='tentative[i-1].man'
            )
            SM-person-box(
              :gender='"w"'
              :index='tentative[i-1].woman'
            )
      div.col-xs-6
        div.row(v-for='i in n' v-if='i > (n + 1) / 2')
          h4 {{i}}.)
          div.matching(v-if='tentative[i-1]')
            SM-person-box(
              :gender='"m"'
              :index='tentative[i-1].man'
            )
            SM-person-box(
              :gender='"w"'
              :index='tentative[i-1].woman'
            )
    div(v-if='n <= 3')
      div.col-xs-12
        div.row(v-for='i in n')
          h4 {{i}} .)
          div(v-if='tentative[i-1]')
            SM-person-box(
              :gender='"m"'
              :index='tentative[i-1].man'
            )
            SM-person-box(
              :gender='"w"'
              :index='tentative[i-1].woman'
            )

</template>

<script>
import SMPersonBox from './SM-PersonBox';

export default {
  components: {
    SMPersonBox,
  },
  data() {
    return {
    };
  }, // end data
  computed: {
    n() { return this.$store.state.stableMarriage.problemSize; },
    tentative() { return this.$store.state.stableMarriage.tentatives; },
  }, // end computed
};
</script>

<style scoped>
div.row {
  margin-bottom: 5px;
}
div.row h4 {
  display: inline-block;
  margin-top:20px;
  margin-right: 5px;
  margin-left: 5px;
}
div.matching {
  display: inline-block
}
</style>


